      :zoom="zoom"
      style="width:640px; height:360px; margin: 32px auto;"
      ref="mapRef"
      @dragend="handleDrag"
  ></GmapMap>-->
  <MglMap
    :accessToken="accessToken"
    :mapStyle="mapStyle"
    :center="myCoordinates"
    :mapboxGl="mapbox.gl"
    @load="onMapLoaded"
    :zoom="zoom"
    ref="mapRef"
    :attributionControl="false"
  >
    <!-- hash -->
    <MglGeolocateControl position="top-right" :trackUserLocation="true" :showUserLocation="false" />
    <MglFullscreenControl />

    <MglMarker :coordinates="myCoordinates">
      <v-avatar slot="marker">
        <img
          src="https://avatars3.githubusercontent.com/u/43021528?s=460&u=d833cef76869dfaeacc8dbc110718c4bc8e1b02d&v=4"
        />
      </v-avatar>
      <MglPopup :coordinates="myCoordinates" :onlyText="true" anchor="top">
        <VCard>
          <div>Voc</div>
        </VCard>
      </MglPopup>
    </MglMarker>
  </MglMap>
</template>
<script>
import Mapbox from "mapbox-gl";
import {
  MglMap,
  MglGeolocateControl,
  MglFullscreenControl,
  MglMarker,
  MglPopup
} from "vue-mapbox";

export default {
  components: {
    MglMap,
    MglGeolocateControl,
    MglFullscreenControl,
    MglMarker,
    MglPopup
  },
  data() {
    return {
      map: null,
      //   coordinates: [-54.26867, -28.28287],
      myCoordinates: {
        lat: 16,
        lng: 33
      },
      zoom: 1.54,
      accessToken:
        "pk.eyJ1Ijoic29jaWFsbWF0ZSIsImEiOiJja2NmZHFiMGEwMWQ5MndsaWVzaGxjMHB6In0.Lp8pyQ2bmdw9rnSzkEtTdA", // your access token. Needed if you using Mapbox maps
      mapStyle: "mapbox://styles/mapbox/streets-v11" // your map style
    };
  },
  created() {
    // We need to set mapbox-gl library here in order to use it in template
    this.mapbox = Mapbox;

    // does the user have a saved center? use it instead of the default
    if (localStorage.center) {
      this.myCoordinates = JSON.parse(localStorage.center);
    } else {
      // get user's coordinates from browser request
      this.$getLocation({})
        .then(coordinates => {
          console.log(coordinates, "COORDENADAS");
          this.myCoordinates = coordinates;
        })
        .catch(error => alert(error));
    }
    // does the user have a saved zoom? use it instead of the default
    if (localStorage.zoom) {
      this.zoom = parseInt(localStorage.zoom);
    }
  },
  mounted() {
    // add the map to a data object
    // this.$refs.mapRef.$mapPromise.then(map => (this.map = map));
  },
  methods: {
    // Esse metodo no existia ai dava falha na construo
    onMapLoaded() {
      console.log("loaded");
    }
    // handleDrag() {
    //   // get center and zoom level, store in localstorage
    //   let center = {
    //     lat: this.map.getCenter().lat(),
    //     lng: this.map.getCenter().lng()
    //   };
    //   let zoom = this.map.getZoom();
    //   localStorage.center = JSON.stringify(center);
    //   localStorage.zoom = zoom;
    // }
  },
  computed: {
    mapCoordinates() {
      if (!this.map) {
        return {
          lat: 0,
          lng: 0
        };
      }
      return {
        lat: this.map
          .getCenter()
          .lat()
          .toFixed(4),
        lng: this.map
          .getCenter()
          .lng()
          .toFixed(4)
      };
    }
  }
};
</script>


